	import quickBtn from '@/static/mbcImg/images/common/quickBtn.png';
	import { mapMutations } from 'vuex';
    export default {
        name: 'quickBtn',
        components: {
        },
        data () {
          return {
				quickBtn: quickBtn,
				quickNav:{
					show: false
				}
          };
        },
        methods: {
          ...mapMutations({
            setQuickNavCO: 'setQuickNavCO'
          }),
          clickQuickBtn () {
            this.quickNav.show = true;
            this.$store.commit('setQuickNavCO', this.quickNav); // 更新setAuthShow
          }
        }
    };
